<template>
    <div id="singleBlog">
        <h1>{{blog.title}}</h1>
        <article>{{blog.content}}</article>
        <p>作者:{{blog.author}}</p>
        <p>分类:</p>
        <ul>
            <li v-for="category in blog.categories">{{category}}</li>
        </ul>
    </div>
</template>
<script>
export default {
    name:'single-blog',
    data(){
        return{
            // 接收传过来的Id
            id:this.$route.params.id,
            blog:{}
        }
    },
    created(){
        // 请求数据并把点击传过来的id带入
        this.$axios.get('https://wd4254457889oggbip.wilddogio.com/posts/'+this.id+'.json')
        .then(res =>{
            console.log(res)
            this.blog = res.data
        })
    }
}
</script>
<style scoped>
#singleBlog{
    max-width: 960px;
    margin: 0 auto;
    padding: 20px;
    background-color: #eee;
    border: 1px dotted #aaa;
}
a{
    text-decoration: none;
}
</style>





